body {
    background-color: #60A171;
    position: relative;
    margin: 0;
    padding: 0;
    font-family: Arial, sans-serif;
}

.nav-bar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 60px;
    padding: 0 20px;
    background-color: #193536;
    color: white;
    position: sticky;
    top: 0;
    z-index: 100;
    border-bottom: 1px solid #193536;
    box-sizing: border-box;
    overflow: hidden; /* Ensures content doesn't overflow */
}

.nav-bar-left {
    display: flex;
    align-items: center;
    flex-shrink: 0; /* Prevents shrinking */
}

.nav-bar-left img.logo {
    height: 50px;
    width: 50px;
    border-radius: 10px;
    margin-right: 10px;
}

.nav-bar-right {
    display: flex;
    align-items: center;
    flex-shrink: 0; /* Prevents shrinking */
    overflow: hidden; /* Ensures content doesn't overflow */
    flex-wrap: wrap; /* Allows wrapping of buttons if necessary */
}

.nav-bar-right ul {
    display: flex;
    padding: 0;
    margin: 0;
    list-style: none;
}

.nav-bar-right li {
    margin-left: 10px;
}

.nav-bar-right a {
    background-color: #104f48;
    color: white;
    border: none;
    padding: 10px 20px;
    cursor: pointer;
    border-radius: 5px;
    text-decoration: none; /* Removes underline */
    white-space: nowrap; /* Prevents text from wrapping */
    display: inline-block;
}

.nav-bar-right a:hover {
    background-color: #0c3f3a;
}

.pred-container {
    display: flex;
    height: calc(100vh - 60px); /* Full viewport height minus navbar height */
    padding-top: 20px; /* Ensure the content starts below the fixed navbar */
}

.dropdown {
    padding: 1rem;
    display: flex;
    flex-direction: column;
    align-items: stretch; /* Stretch filters to fill height */
    width: 20%; /* Adjust this width as needed */
    max-width: 300px;
    font-weight: bold;
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-size: 14px;
    text-align: left;
    word-wrap: break-word; /* Ensure paragraph text wraps if too long */
    word-break: break-word; /* Break long words at arbitrary points if necessary */
    white-space: normal;
    box-sizing: border-box;
    color: white;
}

p {
    word-wrap: break-word; /* Ensure paragraph text wraps if too long */
    word-break: break-word; /* Break long words at arbitrary points if necessary */
    white-space: normal;
    font-family: Georgia, 'Times New Roman', Times, serif;
}

.link {
    color: #0A63F5;
    text-decoration: none; /* Removes underline */
}

.link:hover {
    color: red;
}

#main {
    flex-grow: 1; /* Ensures main takes up the rest of the space */
    margin-top: 0.5%;
    margin-left: 1%;
    margin-right: 1%;
    height: 92%;
    background-color: #193536;
    border: 1px solid #237A65;
    border-radius: 3px;
    box-shadow: inset -2px -5px 10px 0px rgba(255, 255, 255, 0.2), inset 5px 5px 10px 0px rgba(0, 0, 0, 0.2);
    overflow-y: auto;
    font-family: Georgia, 'Times New Roman', Times, serif;
}

/* Responsive Styles */
@media (max-width: 768px) {
    .nav-bar {
        flex-direction: column;
        height: auto;
        padding: 10px;
    }

    .nav-bar-left, .nav-bar-right {
        width: 100%;
        justify-content: center;
        margin: 5px 0;
    }

    .nav-bar-left img.logo {
        height: 40px;
        width: 40px;
        margin-right: 5px;
    }

    .nav-bar-right {
        flex-direction: column;
        align-items: center; /* Center align buttons when stacked */
    }

    .nav-bar-right li {
        margin-left: 0;
        margin-bottom: 5px;
    }

    .nav-bar-right a {
        width: auto;
        padding: 10px;
        margin-bottom: 5px; /* Added to provide spacing between buttons */
    }

    .pred-container {
        flex-direction: column;
    }

    .dropdown {
        width: 100%;
        max-width: none;
    }

    #main {
        width: 100%;
        margin-left: 0;
    }
}
