.pred-page{
    background-color: darkgoldenrod;
    color: white;
}

.pred-form{
    /* background-color: #88AB90; */
    /* background-color: #eef3ef; */
    height: 100%;
    width: 50%;
    display: flex;
    flex-direction: column;  /* Arrange elements vertically */
    text-align: left;    /*Center elements horizontally*/
    justify-content: center; /*Center elements vertically*/
    border: 0px solid;
    border-radius: 5px;
    margin: auto;
    /* background-color:#237A65; */
    /* box-shadow: 5px 5px 5px 5px rgba(0.2, 0.2, 0.2, 0.2); */
    padding-left: 250px;
    padding-top: 10px;
    padding-bottom: 10px;
    position: relative;
    left: 0;
    /* color: #0A63F5; */
    color: #83E8BA;
    
}

.pred-form.hide {
    /* Shift form to the left */
    left: -50%;
}

.pred-form input{
    width: 300px;
    border-radius: 2px;
}

.pred-form select{
    width: 300px;
    border-radius: 2px;
}

button{
    width: 100px;
}

button:hover{
    cursor: pointer;
    color: black;
    font-size: 15px;
}

label{
    font-weight: bold;
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-size: 16px;
}

h2 {
    color: white;
}
