html {
    margin: 0; padding: 0;
    background-repeat:no-repeat;
    min-height: 100%;
}

#login-page {
    margin: 0; padding: 0;
    background-repeat:no-repeat;
    min-height: 100%;
}


body {
    /* background: rgb(91,83,85);
    background: linear-gradient(107deg, rgba(91,83,85,1) 0%, rgba(48,38,39,1) 51%, rgba(0,0,0,1) 98%); */
    background-color: #64916E;
    background-image: url('./media/dark-green-marble-wave-background.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    -webkit-backdrop-filter: blur(8px);
    backdrop-filter: blur(8px);
    min-height: 100%;
    /* margin: 0; */
    padding: auto;
}

#login-container{
    display: flex;
    flex-direction: row;
    border: 0px solid;
    border-radius: 5px;
    margin-top: 5%;
    margin-left: 10%;
    /* padding: auto; */
    width:80vw;  /* Stretch container to full viewport width */
    height: 80vh;  /* Stretch container to full viewport height (optional) */
    background-color:white;
    color: white;
    /* box-shadow: 0px 5px 5px rgba(0, 0, 0, 0.2); */
    box-shadow: 5px 5px 5px 5px rgba(0.2, 0.2, 0.2, 0.2);
}

#img-div{
    /* background-color: #88AB90; */
    height: 100%;
}

img{
    width: 100%;
    height: 100%;
    position: static;
    margin-top: 0px;
}

.login-div{
    width: 50%;
    align-content: center;
}

h1{
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-size: xx-large;
    text-align: center;
    margin-bottom: 20px;
}

h2{
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-size:large;
    text-align: center;
    margin-bottom: 20px;
}

.login-form {
    /* background-color: #88AB90; */
    background-image: url('./media/dark-green-marble-wave-background.jpg');
    /* filter: blur(8px); */
    /* -webkit-filter: blur(8px); */
    background-color: #64916E;
    height: 100%;
    display: flex;
    flex-direction: column;  /* Arrange elements vertically */
    align-items: center;    /*Center elements horizontally*/
    justify-content: center; /*Center elements vertically*/
}
  
.label {
    /* font-weight: bold; */
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-size: 16px;
    /* margin-left: -40%; */
}

.input {
    padding: 10px;
    margin-bottom: 10px;
    border-radius: 5px;
    border: 1px solid #ccc;
    width: 50%;
    /* text-align: center; */
}

button {
    margin-top: 10px;
    padding: 10px 20px;
    /* background-color: #4CAF50; */
    background-color: #0A63F5;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

a {
    color: #0A63F5;
}
