body {
    margin: 0;
    padding: 0;
    font-family: Arial, sans-serif;
}

.nav-bar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 60px;
    padding: 0 20px;
    background-color: #193536;
    color: white;
    position: sticky;
    top: 0;
    z-index: 100;
    border-bottom: 1px solid #193536;
    box-sizing: border-box;
    overflow: hidden; /* Ensures content doesn't overflow */
}

.nav-bar-left {
    display: flex;
    align-items: center;
    flex-shrink: 0; /* Prevents shrinking */
}

.nav-bar-left img.logo {
    height: 50px;
    width: 50px;
    border-radius: 10px;
    margin-right: 10px;
}

.nav-bar-right {
    display: flex;
    align-items: center;
    flex-shrink: 0; /* Prevents shrinking */
    overflow: hidden; /* Ensures content doesn't overflow */
    flex-wrap: wrap; /* Allows wrapping of buttons if necessary */
}

.nav-bar-right li {
    list-style-type: none;
    margin-left: 10px;
}

.nav-bar-right a {
    background-color: #104f48;
    color: white;
    border: none;
    padding: 10px 20px;
    cursor: pointer;
    border-radius: 5px;
    text-decoration: none; /* Removes underline */
    white-space: nowrap; /* Prevents text from wrapping */
    display: inline-block;
}

.nav-bar-right a:hover {
    background-color: #0c3f3a;
}

.chart-container {
    background-color: white;
    color: black;
    width:80%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 30px;
    padding-left: 3%;
    padding-right: 3%;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
}
  
.chart-container canvas {
    width: 100% !important;
    height: 100% !important;
}
  
.chart{
    margin: 20px;
}

#pie{
    width: 50%;
    height: 50%;
}

#info{
    width: 40%;
    text-align: center;
    margin:auto;
    background-color: white;
    border-radius: 5px;
    padding-right: 15px;
}

#info ul{
    list-style-type: none;
}

button{
    margin-right: 2px;
    margin-left: 2px;
}

