body {
    margin: 0;
    padding: 0;
    font-family: Arial, sans-serif;
}

.nav-bar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 60px;
    padding: 0 20px;
    background-color: #193536;
    color: white;
    position: sticky;
    top: 0;
    z-index: 100;
    border-bottom: 1px solid #193536;
    box-sizing: border-box;
    overflow: hidden; /* Ensures content doesn't overflow */
}

.nav-bar-left {
    display: flex;
    align-items: center;
    flex-shrink: 0; /* Prevents shrinking */
}

.nav-bar-left img.logo {
    height: 50px;
    width: 50px;
    border-radius: 10px;
    margin-right: 10px;
}

.nav-bar-right {
    display: flex;
    align-items: center;
    flex-shrink: 0; /* Prevents shrinking */
    overflow: hidden; /* Ensures content doesn't overflow */
    flex-wrap: wrap; /* Allows wrapping of buttons if necessary */
}

.nav-bar-right li {
    list-style-type: none;
    margin-left: 10px;
}

.nav-bar-right a {
    background-color: #104f48;
    color: white;
    border: none;
    padding: 10px 20px;
    cursor: pointer;
    border-radius: 5px;
    text-decoration: none; /* Removes underline */
    white-space: nowrap; /* Prevents text from wrapping */
    display: inline-block;
}

.nav-bar-right a:hover {
    background-color: #0c3f3a;
}

.map-container {
    padding: 20px;
    padding-top: 80px; /* Ensure the content starts below the fixed navbar */
    box-sizing: border-box;
}

.map {
    width: 100%;
}

select {
    margin-bottom: 30px;
}

/* Responsive Styles */
@media (max-width: 768px) {
    .nav-bar {
        flex-direction: column;
        height: auto;
        padding: 10px;
    }

    .nav-bar-left, .nav-bar-right {
        width: 100%;
        justify-content: center;
        margin: 5px 0;
    }

    .nav-bar-left img.logo {
        height: 40px;
        width: 40px;
        margin-right: 5px;
    }

    .nav-bar-right {
        flex-direction: column;
        align-items: center; /* Center align buttons when stacked */
    }

    .nav-bar-right li {
        margin-left: 0;
        margin-bottom: 5px;
    }

    .nav-bar-right a {
        width: auto;
        padding: 10px;
        margin-bottom: 5px; /* Added to provide spacing between buttons */
    }
}
